// src/components/skills/RequestExchangeDialog.jsx
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogFooter,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Label } from "@/components/ui/label";
import { useState } from "react";
import { Calendar } from "@/components/ui/calendar";
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover";
import { CalendarIcon } from "lucide-react";
import { format } from "date-fns";
import { cn } from "@/lib/utils";
import toast, { Toaster } from "react-hot-toast";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";

export function RequestExchangeDialog({ isOpen, onClose, skill }) {
  const [formData, setFormData] = useState({
    date: null,
    timePreference: "",
    goals: "",
    message: "",
    duration: "",
  });

  if (!skill) return null;

  const handleSubmit = (e) => {
    e.preventDefault();

    // Validate form
    if (
      !formData.date ||
      !formData.timePreference ||
      !formData.duration ||
      !formData.goals
    ) {
      toast.error("Please fill in all required fields", {
        style: {
          background: "#363636",
          color: "#fff",
        },
      });
      return;
    }

    // Show success message
    toast.success(
      `Exchange request sent to ${skill.provider.name} for ${skill.title}`,
      {
        duration: 3000,
        style: {
          background: "#363636",
          color: "#fff",
        },
      }
    );

    // Reset form and close dialog
    setFormData({
      date: null,
      timePreference: "",
      goals: "",
      message: "",
      duration: "",
    });
    onClose();
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="sm:max-w-[425px]">
        <DialogHeader>
          <DialogTitle>Request Skill Exchange</DialogTitle>
          <DialogDescription>
            Request to learn {skill?.title} from {skill?.provider.name}
          </DialogDescription>
        </DialogHeader>

        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="space-y-2">
            <Label>Preferred Start Date</Label>
            <Popover>
              <PopoverTrigger asChild>
                <Button
                  variant={"outline"}
                  className={cn(
                    "w-full justify-start text-left font-normal",
                    !formData.date && "text-muted-foreground"
                  )}
                >
                  <CalendarIcon className="mr-2 h-4 w-4" />
                  {formData.date ? format(formData.date, "PPP") : "Pick a date"}
                </Button>
              </PopoverTrigger>
              <PopoverContent className="w-auto p-0" align="start">
                <Calendar
                  mode="single"
                  selected={formData.date}
                  onSelect={(date) => setFormData({ ...formData, date })}
                  initialFocus
                />
              </PopoverContent>
            </Popover>
          </div>

          <div className="space-y-2">
            <Label>Preferred Time</Label>
            <Select
              value={formData.timePreference}
              onValueChange={(value) =>
                setFormData({ ...formData, timePreference: value })
              }
            >
              <SelectTrigger>
                <SelectValue placeholder="Select preferred time" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="morning">Morning (9 AM - 12 PM)</SelectItem>
                <SelectItem value="afternoon">
                  Afternoon (1 PM - 5 PM)
                </SelectItem>
                <SelectItem value="evening">Evening (6 PM - 9 PM)</SelectItem>
              </SelectContent>
            </Select>
          </div>

          <div className="space-y-2">
            <Label>Exchange Duration</Label>
            <Select
              value={formData.duration}
              onValueChange={(value) =>
                setFormData({ ...formData, duration: value })
              }
            >
              <SelectTrigger>
                <SelectValue placeholder="Select duration" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="4">4 Hours (2 Credits)</SelectItem>
                <SelectItem value="8">8 Hours (4 Credits)</SelectItem>
                <SelectItem value="12">12 Hours (6 Credits)</SelectItem>
                <SelectItem value="custom">Custom Duration</SelectItem>
              </SelectContent>
            </Select>
          </div>

          <div className="space-y-2">
            <Label>Learning Goals</Label>
            <Textarea
              placeholder="What specific skills or topics would you like to learn?"
              value={formData.goals}
              onChange={(e) =>
                setFormData({ ...formData, goals: e.target.value })
              }
            />
          </div>

          <div className="space-y-2">
            <Label>Message to Instructor</Label>
            <Textarea
              placeholder="Introduce yourself and explain why you're interested in learning this skill..."
              value={formData.message}
              onChange={(e) =>
                setFormData({ ...formData, message: e.target.value })
              }
            />
          </div>

          <DialogFooter>
            <Button type="button" variant="outline" onClick={onClose}>
              Cancel
            </Button>
            <Button
              type="submit"
              className="bg-gradient-to-r from-red-700 to-red-500 text-white"
            >
              Send Request
            </Button>
          </DialogFooter>
        </form>
      </DialogContent>
    </Dialog>
  );
}