// src/components/skills/ExchangeCard.jsx
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Clock, Calendar } from "lucide-react";
import { useState } from "react";
import { ScheduleSessionDialog } from "./ScheduleSessionDialog";
import { SessionDetailsDialog } from "./SessionDetailsDialog";

export function ExchangeCard({ exchange }) {
  const {
    skill,
    with: partner,
    type,
    progress,
    nextSession,
    totalHours,
    completedHours,
    image,
  } = exchange;
  const [isScheduleOpen, setIsScheduleOpen] = useState(false);
  const [isDetailsOpen, setIsDetailsOpen] = useState(false);

  return (
    <div className="rounded-lg border bg-card text-card-foreground shadow-sm">
      <div className="p-6">
        <div className="flex items-start justify-between">
          <div className="flex items-center space-x-4">
            <Avatar className="h-12 w-12">
              <AvatarImage src={image} />
              <AvatarFallback>
                {partner
                  .split(" ")
                  .map((n) => n[0])
                  .join("")}
              </AvatarFallback>
            </Avatar>
            <div>
              <h3 className="text-lg font-semibold">{skill}</h3>
              <p className="text-sm text-muted-foreground">
                {type === "Learning" ? "Learning from" : "Teaching"} {partner}
              </p>
            </div>
          </div>
          <Badge
            variant={type === "Learning" ? "default" : "secondary"}
            className={
              type === "Learning"
                ? "bg-blue-100 dark:bg-blue-900 text-blue-700 dark:text-blue-100"
                : "bg-green-100 dark:bg-green-900 text-green-700 dark:text-green-100"
            }
          >
            {type}
          </Badge>
        </div>

        <div className="mt-4 space-y-2">
          <div className="flex justify-between text-sm text-muted-foreground">
            <span>Progress</span>
            <span>{progress}%</span>
          </div>
          <div className="h-2 w-full bg-slate-200 dark:bg-slate-800 rounded-full">
            <div
              className="h-full bg-gradient-to-r from-red-700 to-red-300 rounded-full transition-all"
              style={{ width: `${progress}%` }}
            />
          </div>
        </div>

        <div className="mt-4 grid grid-cols-2 gap-4">
          <div className="space-y-1">
            <div className="text-sm text-muted-foreground flex items-center">
              <Clock className="mr-2 h-4 w-4" />
              Hours Completed
            </div>
            <p className="font-medium">
              {completedHours} / {totalHours} hours
            </p>
          </div>
          <div className="space-y-1">
            <div className="text-sm text-muted-foreground flex items-center">
              <Calendar className="mr-2 h-4 w-4" />
              Next Session
            </div>
            <p className="font-medium">{nextSession}</p>
          </div>
        </div>

        <div className="mt-4 flex gap-2">
          <Button
            className="flex-1 bg-gradient-to-r  bg-red-500 text-white"
            onClick={() => setIsScheduleOpen(true)}
          >
            Schedule Session
          </Button>
          <Button
            variant="outline"
            className="flex-1 hover:bg-gradient-to-r hover:from-blue-500/10 hover:to-purple-500/10"
            onClick={() => setIsDetailsOpen(true)}
          >
            View Details
          </Button>
        </div>
      </div>

      <ScheduleSessionDialog
        isOpen={isScheduleOpen}
        onClose={() => setIsScheduleOpen(false)}
        exchange={exchange}
      />

      <SessionDetailsDialog
        isOpen={isDetailsOpen}
        onClose={() => setIsDetailsOpen(false)}
        exchange={exchange}
      />
    </div>
  );
}
