export default function SkillsPage() {
  const [searchTerm, setSearchTerm] = useState("");
  const [filterCategory, setFilterCategory] = useState("all");
  const [filterLevel, setFilterLevel] = useState("all");
  const [skills, setSkills] = useState(skillsData);
  const [exchanges, setExchanges] = useState(activeExchanges);
  const [isAddSkillOpen, setIsAddSkillOpen] = useState(false);
  const [selectedSkill, setSelectedSkill] = useState(null);

  // Filter skills based on search and filters
  const filteredSkills = skills.filter((skill) => {
    const matchesSearch =
      skill.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
      skill.description.toLowerCase().includes(searchTerm.toLowerCase());

    const matchesCategory =
      filterCategory === "all" || skill.category === filterCategory;
    const matchesLevel = filterLevel === "all" || skill.level === filterLevel;

    return matchesSearch && matchesCategory && matchesLevel;
  });

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        {/* Left Side: Title & Description */}
        <div>
          <h1 className="text-3xl font-bold tracking-tight text-gray-900 dark:text-white">
            Skills Marketplace
          </h1>
          <p className="text-gray-600 dark:text-gray-200">
            Exchange knowledge and expertise with fellow alumni.
          </p>
        </div>

        {/* Right Side: Tokens & Coins Display */}
        <div className="flex items-center gap-4 bg-gray-100 dark:bg-gray-800 px-6 py-3 rounded-full shadow-md">
          {/* Coin Icon */}
          <div className="relative">
            <img
              src="/coin.png" // Replace with actual coin image
              alt="Token"
              className="h-10 w-10"
            />
            <span className="absolute -top-2 -right-3 bg-red-600 text-white text-xs px-2 py-1 rounded-full shadow-lg">
              +5
            </span>
          </div>

          {/* Coin Details */}
          <div>
            <p className="text-sm text-gray-700 dark:text-gray-300">
              Available Tokens
            </p>
            <span className="text-xl font-bold text-gray-900 dark:text-white">
              45
            </span>
          </div>
        </div>
      </div>
      {/* Stats Overview */}
      <Tabs defaultValue="browse" className="space-y-6">
        <div className="flex items-center justify-between">
          <TabsList>
            <TabsTrigger value="browse">Browse Skills</TabsTrigger>
            <TabsTrigger value="exchanges">Active Exchanges</TabsTrigger>
            <TabsTrigger value="progress">Skills Progress</TabsTrigger>
          </TabsList>
          <Button
            className="bg-gradient-to-r from-red-700 to-red-500 text-white"
            onClick={() => setIsAddSkillOpen(true)}
          >
            <Plus className="mr-2 h-4 w-4" />
            List a Service
          </Button>
        </div>

        <TabsContent value="browse" className="space-y-6">
          <Card>
            <CardContent className="pt-6">
              <SkillSearch
                searchTerm={searchTerm}
                onSearchChange={setSearchTerm}
                filterCategory={filterCategory}
                onCategoryChange={setFilterCategory}
                filterLevel={filterLevel}
                onLevelChange={setFilterLevel}
              />
            </CardContent>
          </Card>

          <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
            {filteredSkills.map((skill) => (
              <SkillCard
                key={skill.id}
                skill={skill}
                onRequest={() => setSelectedSkill(skill)}
              />
            ))}
          </div>
        </TabsContent>

        <TabsContent value="exchanges" className="space-y-6">
          <div className="grid gap-6 md:grid-cols-2">
            {exchanges.map((exchange) => (
              <ExchangeCard key={exchange.id} exchange={exchange} />
            ))}
          </div>
        </TabsContent>

        <TabsContent value="progress" className="space-y-6">
          <SkillsProgress />
        </TabsContent>
      </Tabs>

      {/* Dialogs */}
      <AddSkillDialog
        isOpen={isAddSkillOpen}
        onClose={() => setIsAddSkillOpen(false)}
      />

      <RequestExchangeDialog
        isOpen={!!selectedSkill}
        onClose={() => setSelectedSkill(null)}
        skill={selectedSkill}
      />
      {/* Toaster for notifications */}
      <Toaster position="top-right" />
    </div>
  );
}
